//const's DOM element's

const headerImg = document.querySelector('.image__header');
const section = document.querySelector('.header');
const centerHeader = document.querySelector('.center');
const aboutUs = document.querySelector('.about_us');
const aboutImage = document.querySelector('.center-box__image_about_us');
const infoAboutUs = document.querySelector('.center-box__info_about_us');
const contsctUs = document.querySelector('.contact_us');
const portfolio = document.querySelector('.portfolio');
const menu = document.querySelector('.manu__header');
const butShow = document.querySelectorAll('.show');
const showWork = document.querySelector('.show-work ');
const closeModal = document.querySelector('.close-modal');
const imgPrev = document.querySelector('#imgPrev');



//All functions
function showPortfolio(arrayClass){
    for(var i=0; i < arrayClass.length;i++){
        arrayClass[i].addEventListener('click',(e)=>{
            const modal = document.querySelector('.show-work__img-modal');
            let elem = e.target;
            let getImgSrc = elem.getAttribute('href');
            const workImgModal = document.querySelector('.show-work__img-modal')
            
            
            showWork.style.display = 'block';
            console.log(getImgSrc)

                imgPrev.setAttribute('src',getImgSrc);
                workImgModal.style.marginTop = 1000 + 'px';
                workImgModal.style.width = '100%';
                imgPrev.style.height = 'unset';

                if(getImgSrc == 'assets/img/portfolio/1.jpg'){
                    workImgModal.style.marginTop = 150 + 'px';
                    console.log('ads')
                }
                
        })  
    }

    closeModal.addEventListener('click',()=>{
        showWork.style.display = 'none';
    })

    
  
}

section.addEventListener('click',(e)=>{

    // const's get 'TOP'
    const aboutUsTop = aboutUs.getBoundingClientRect().top;
    const portfolioTop = portfolio.getBoundingClientRect().top;
    const contactUsTop = contsctUs.getBoundingClientRect().top;
    

    let TargetElement = e.target.getAttribute('data-url');
    if(TargetElement == 'about'){
        window.scrollTo(0,aboutUsTop)
    }
    if(TargetElement == 'portfolio'){
        window.scrollTo(0,portfolioTop)
    }
    if(TargetElement == 'contact'){
        window.scrollTo(0,contactUsTop - 200)
    }
    

})


function innerDevIcon() {
    let dev = document.querySelector('.dev');
    dev.innerText = '</>'
}


function paralaxElement(container,element){
    container.addEventListener('mousemove',(e)=>{
        let clientX = e.clientX * 1 / 20;
        let clientY = e.clientY * 1 / 20;

        element.style.top = -clientY + 'px';
        element.style.left = -clientX + 'px';
    })
}


function paralaxScroll(elem,secondelem){
    window.addEventListener('scroll',function (e) {
       let scrolled = window.scrollY  /4.5;

        elem.style.marginTop = -scrolled  + 'px'
        secondelem.style.marginTop = -window.scrollY  / 4 + 'px'

    });


}
//

function headerContainer(){
    innerDevIcon();
    paralaxElement(section,headerImg);
    paralaxScroll(centerHeader,headerImg);
}

function aboutUsContainer(){
    paralaxElement(aboutUs,aboutImage);
    paralaxScroll(infoAboutUs,aboutImage);
}

//

headerContainer();
aboutUsContainer();
showPortfolio(butShow)
